@import url("https://fonts.googleapis.com/css?family=Orbitron:500,700|Roboto");
/**
 * Declaring fonts
 */
.section1 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  box-sizing: border-box;
  padding: 135px 20px 0px 20px;
  position: relative; }
  .section1 .content-wrapper {
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    width: 900px; }
    .section1 .content-wrapper .left-content {
      width: 350px;
      display: flex;
      align-items: flex-start;
      justify-content: center;
      flex-flow: column wrap;
      box-sizing: border-box;
      padding: 0px 5px; }
      .section1 .content-wrapper .left-content .RBB {
        display: block;
        border-left: 4px solid #598053;
        border-radius: 4px;
        padding: 0 10px;
        text-align: left;
        font-size: 3rem; }
        .section1 .content-wrapper .left-content .RBB h1 {
          font-size: 74px; }
      .section1 .content-wrapper .left-content .RBB-small {
        display: none;
        border-left: 4px solid #598053;
        border-radius: 4px;
        padding: 0 10px;
        text-align: left;
        font-size: 3rem; }
        .section1 .content-wrapper .left-content .RBB-small h1 {
          font-size: 74px; }
    .section1 .content-wrapper .right-content {
      width: 550px;
      box-sizing: border-box;
      padding: 0px 5px; }
      .section1 .content-wrapper .right-content .computerDiv1 {
        width: 100%;
        display: grid;
        grid-template-columns: 3.8% 75.5% 20.7%;
        grid-template-rows: 17.3% 58% 24.7%; }
        .section1 .content-wrapper .right-content .computerDiv1 .computer {
          grid-column: 1 / 4;
          grid-row: 1 / 4; }
        .section1 .content-wrapper .right-content .computerDiv1 .clientImg {
          max-width: 100%;
          grid-column: 2 / 3;
          grid-row: 2 / 3; }
    .section1 .content-wrapper .threeButtons {
      width: 100%;
      display: flex;
      align-items: flex-end;
      flex-flow: row wrap;
      justify-content: space-between;
      margin: 50px 0px; }
  .section1 .arrow {
    display: block;
    height: 30px;
    width: 30px;
    position: absolute;
    bottom: 50px;
    left: calc(50% - 15px);
    margin: 0 auto;
    cursor: pointer;
    animation: bounce 2s infinite; }

@keyframes bounce {
  0%,
  25%,
  50%,
  75%,
  100% {
    transform: translateY(0); }
  40% {
    transform: translateY(-20px); }
  60% {
    transform: translateY(-12px); } }

@media (max-width: 957px) {
  .section1 {
    padding: 196px 20px 0px 20px; }
    .section1 .content-wrapper .left-content {
      width: 100%;
      margin-bottom: 50px; }
      .section1 .content-wrapper .left-content .RBB {
        display: none; }
      .section1 .content-wrapper .left-content .RBB-small {
        display: block; }
        .section1 .content-wrapper .left-content .RBB-small h1 {
          font-size: 54px; }
    .section1 .arrow {
      bottom: 0px; } }

@media (max-width: 608px) {
  .section1 {
    align-items: flex-start; }
    .section1 .content-wrapper .left-content .RBB-small h1 {
      font-size: 24px; }
    .section1 .content-wrapper .right-content {
      width: 100%; } }

@media (max-width: 522px) {
  .section1 {
    padding: 40px 20px 0px 20px; }
    .section1 .arrow {
      display: none; } }

@media (max-width: 482px) {
  .section1 .content-wrapper .threeButtons {
    width: 100%;
    flex-flow: column wrap; } }
