@import url("https://fonts.googleapis.com/css?family=Orbitron:500,700|Roboto");
/**
 * Declaring fonts
 */
.categories {
  width: 100%;
  max-width: 900px;
  margin: 0 auto; }
  .categories h1 {
    padding: 40px 0 20px 0; }
  .categories .container {
    display: flex;
    flex-flow: row wrap;
    justify-content: center; }
    .categories .container .button-wrapper {
      display: flex;
      justify-content: center;
      align-items: flex-end;
      flex-grow: 1;
      width: 100%; }
  .categories .category {
    display: flex;
    flex-flow: column wrap;
    width: 200px;
    min-width: 180px;
    margin: 10px 10px 50px 10px;
    padding: 10px;
    box-sizing: border-box;
    color: #ede4d7;
    position: relative; }
    .categories .category img {
      height: 50px;
      margin-bottom: 10px; }
    .categories .category h3 {
      margin: 10px 0; }
    .categories .category p {
      font-size: 0.8rem;
      margin: 5px 0 0 0; }
    .categories .category button {
      appearance: none;
      outline: none;
      cursor: pointer;
      margin-top: 20px;
      bottom: 0;
      left: 10px;
      right: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: linear-gradient(#66b62d, #457c1e);
      color: #ede4d7;
      font-size: 0.8rem;
      border-radius: 5px;
      height: 35px;
      width: 140px;
      border: 1px solid #7FF071;
      font-family: "Orbitron", sans-serif; }
      .categories .category button:hover {
        background: linear-gradient(#73cc33, #509023); }
