.navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 10px 20px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  background-color: #1d211c;
  z-index: 99; }
  .navigation .logo {
    display: flex;
    flex-flow: row; }
    .navigation .logo .logo-wrapper {
      display: flex; }
    .navigation .logo img {
      height: 55px; }
    .navigation .logo .text {
      color: #ede4d7;
      display: flex;
      flex-flow: column;
      justify-content: center;
      align-items: flex-start; }
      .navigation .logo .text h4 {
        margin: 2px; }
    .navigation .logo .mobile {
      display: none; }
    .navigation .logo .is-mobile {
      display: none; }
  .navigation .menu {
    display: flex;
    flex-flow: row wrap; }
    .navigation .menu .nav-text {
      margin: 0; }
    .navigation .menu a {
      display: inline-block;
      line-height: 35px;
      padding: 0 15px;
      font-size: 1rem;
      color: #ede4d7; }
      .navigation .menu a:hover {
        color: #7FF071; }
    .navigation .menu .signup {
      font-size: 0.7rem;
      line-height: 35px;
      border: 1px solid #7FF071;
      background: linear-gradient(#66b62d, #457c1e);
      border-radius: 5px;
      margin-left: 30px;
      min-width: 70px; }
    .navigation .menu .active {
      color: #ede4d7; }
    .navigation .menu .not-mobile {
      display: flex; }

@media only screen and (max-width: 957px) {
  .navigation {
    flex-flow: column wrap; }
    .navigation .logo {
      width: 100%;
      justify-content: space-between; }
      .navigation .logo .is-mobile {
        display: flex; }
    .navigation .menu .nav-text {
      margin-top: 10px; }
    .navigation .menu .not-mobile {
      display: none; } }

@media only screen and (max-width: 522px) {
  .navigation {
    position: static; }
    .navigation .logo .text {
      text-align: left; }
    .navigation .menu {
      justify-content: center; }
      .navigation .menu .nav-text {
        padding: 0 15px; } }

@media only screen and (max-width: 400px) {
  .navigation {
    padding: 10px 5px; }
    .navigation .logo .signup {
      margin-left: 10px; } }

@media only screen and (max-width: 375px) {
  .navigation .menu {
    padding-top: 5px; }
    .navigation .menu .nav-text {
      padding: 0 20px;
      margin-top: 0px;
      line-height: 30px; } }

@media only screen and (max-width: 350px) {
  .navigation {
    padding: 10px 5px; }
    .navigation .login .button {
      padding: 0; }
    .navigation .logo .signup {
      margin-left: 5px; } }
