@import url("https://fonts.googleapis.com/css?family=Orbitron:500,700|Roboto");
/**
 * Declaring fonts
 */
.registration-form-container {
  user-select: none;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  align-content: center;
  flex-direction: column; }
  .registration-form-container .loading {
    position: absolute;
    width: 200px;
    height: 200px;
    display: none; }
    .registration-form-container .loading.active {
      display: block; }
  .registration-form-container .loadingDone, .registration-form-container .error {
    width: 150px;
    height: 150px;
    display: none; }
    .registration-form-container .loadingDone.active, .registration-form-container .error.active {
      display: block; }

.form-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  height: 50%;
  transition: opacity 0.5s; }
  .form-container.inactive {
    filter: blur(2px);
    opacity: 0.2; }

.input-container-reg {
  width: 100%;
  margin-bottom: 5px;
  box-sizing: border-box; }

.input-container-reg-button {
  width: 100%;
  margin-bottom: 0px;
  box-sizing: border-box; }

.form-input, #form-submit, .register-new-button {
  width: 100%;
  font-size: 16px;
  border-radius: 3px;
  box-sizing: border-box;
  border: none; }

.form-input {
  box-sizing: border-box;
  background-color: #EDE4D7;
  padding: 15px 10px;
  outline: 0; }
  .form-input.valid {
    box-shadow: inset 0 0 0 3px #43AF3D; }
  .form-input.invalid {
    box-shadow: inset 0 0 0 3px #AE4848; }

.errorMessage {
  padding-top: 15px;
  color: #AE4848;
  width: 100%;
  font-family: 'Roboto', sans-serif; }

#form-submit {
  padding: 16px 10px 14px 10px;
  background-color: #2C821F;
  box-shadow: inset 0px 0px 0px 2px #43AF3D;
  color: #EDE4D7;
  font-family: 'Orbitron', sans-serif;
  outline: none;
  opacity: 0.4;
  transition: opacity 0.2s; }
  #form-submit.active {
    cursor: pointer;
    opacity: 1; }
    #form-submit.active:active {
      background-color: #2C821F; }
    #form-submit.active:hover {
      background-color: #43AF3D; }

.interest-text {
  color: #EDE4D7;
  font-family: "Roboto",sans-serif;
  padding: 10px 0 10px 0; }
  .interest-text .remove-interest {
    color: #2C821F;
    text-decoration: underline;
    user-select: none;
    cursor: pointer; }
    .interest-text .remove-interest:hover {
      color: #43AF3D; }

.done-loading-container {
  width: 100%; }
  .done-loading-container .register-new-button {
    padding: 16px 10px 14px 10px;
    background-color: #2C821F;
    box-shadow: inset 0px 0px 0px 2px #43AF3D;
    color: #EDE4D7;
    font-family: 'Orbitron', sans-serif;
    outline: none;
    transition: background-color 0.2s;
    cursor: pointer; }
    .done-loading-container .register-new-button:hover {
      background-color: #43AF3D; }
    .done-loading-container .register-new-button:active {
      background-color: #2C821F;
      opacity: 0.9;
      transition: background-color 0; }

#errortext {
  color: #AE4848;
  width: 100%;
  margin: 0 0 5px 0; }
