.download {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  box-sizing: border-box;
  padding: 135px 20px 100px 20px; }
  .download .download-wrapper {
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    width: 900px; }
    .download .download-wrapper .left-container {
      width: 350px;
      display: flex;
      align-items: flex-start;
      justify-content: center;
      flex-flow: column wrap;
      box-sizing: border-box;
      padding: 0px 5px; }
      .download .download-wrapper .left-container .left-download-content {
        position: relative;
        display: block;
        border-left: 4px solid #598053;
        border-radius: 4px;
        padding: 0 10px;
        text-align: left;
        font-size: 3rem; }
        .download .download-wrapper .left-container .left-download-content .logoDiv {
          display: flex;
          flex-flow: row;
          margin-bottom: 20px;
          margin-left: -25px; }
          .download .download-wrapper .left-container .left-download-content .logoDiv #logo {
            height: 150px; }
          .download .download-wrapper .left-container .left-download-content .logoDiv #logoText {
            color: #ede4d7;
            display: flex;
            flex-flow: column;
            justify-content: center;
