          .download .download-wrapper .left-container .left-download-content #dlBtn .overlay {
            width: 100%;
            height: 100%;
            border: 1px solid #1d211c;
            border-radius: 5px;
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: rgba(0, 0, 0, 0.9); }
            .download .download-wrapper .left-container .left-download-content #dlBtn .overlay .overlay-content {
              display: flex;
              align-items: center; }
          .download .download-wrapper .left-container .left-download-content #dlBtn #dlText {
