            width: 20px;
            float: right;
            cursor: pointer; }
        .download .download-wrapper .left-container .left-download-content #dlBtn-secondary {
          position: relative;
          display: flex;
          justify-content: center;
          align-items: center;
          background: linear-gradient(#66b62d, #457c1e);
          border: 1px solid #7FF071;
          border-radius: 7px;
          color: #ede4d7;
          cursor: pointer;
          width: 300px;
          height: 60px;
          padding: 10px 20px;
          margin-top: 10px;
          pointer-events: none; }
          .download .download-wrapper .left-container .left-download-content #dlBtn-secondary:hover {
            background: linear-gradient(#73cc33, #509023); }
          .download .download-wrapper .left-container .left-download-content #dlBtn-secondary .overlay {
            width: 100%;
            height: 100%;
            border: 1px solid #1d211c;
            border-radius: 5px;
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: rgba(0, 0, 0, 0.9); }
            .download .download-wrapper .left-container .left-download-content #dlBtn-secondary .overlay .overlay-content {
              display: flex;
              align-items: center; }
          .download .download-wrapper .left-container .left-download-content #dlBtn-secondary #dlText {
            display: inline-block;
            font-size: 18pt;
            font-weight: lighter;
            padding-top: 2px; }
          .download .download-wrapper .left-container .left-download-content #dlBtn-secondary .dlBtnImg {
            margin-left: 30px;
