.footer {
  min-height: 20vh;
  background: #1C1818;
  display: flex;
  padding: 60px 0;
  margin: 0 auto; }
  .footer .footer-container {
    width: 100%;
    display: flex;
    justify-content: center;
    text-align: center;
    flex-wrap: wrap; }
    .footer .footer-container .box {
      display: flex;
      align-items: flex-start;
      justify-content: center; }
      .footer .footer-container .box .context-container {
        display: flex;
        align-items: center;
        flex-flow: column; }
        .footer .footer-container .box .context-container h4 {
          display: block;
          color: #ede4d7;
          margin-bottom: 5px;
          font-size: 24px; }
        .footer .footer-container .box .context-container p {
          font-size: 0.9rem;
          color: #ede4d7; }
        .footer .footer-container .box .context-container a {
          font-size: 0.9rem;
          font-family: "Roboto", sans-serif;
          color: #ede4d7; }
          .footer .footer-container .box .context-container a:hover {
            color: #7FF071; }
    .footer .footer-container .copyright {
      grid-area: copy;
      padding: 30px 0 30px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #ede4d7;
      font-size: 0.9rem;
      font-family: "Roboto", sans-serif;
      opacity: 0.7; }

@media only screen and (max-width: 600px) {
  .box {
    width: 100%;
    justify-content: center;
    padding: 20px 0 20px;
    line-height: 20px; } }
