@import url("https://fonts.googleapis.com/css?family=Orbitron:500,700|Roboto");
/**
 * Declaring fonts
 */
html {
  scroll-behavior: smooth; }

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  background: url("/images/background.png") #1C1818;
  background-size: 100% auto;
  background-repeat: repeat-y; }

section {
  min-height: calc(100vh - 160px);
  padding: 80px; }

h1,
h2,
h3 {
  font-family: "Orbitron", sans-serif;
  font-weight: 700;
  margin: 0;
  color: #ede4d7; }

h4,
h5,
h6 {
  font-family: "Orbitron", sans-serif;
  font-weight: 500;
  margin: 0; }

p {
  /* font-family: "Orbitron", sans-serif; */
  font-weight: 500;
  margin: 0;
  color: #ede4d7; }

a {
  font-family: "Orbitron", sans-serif;
  font-weight: 500;
  text-decoration: none;
  margin: 0; }
