.login {
  display: inline-flex;
  position: relative;
  align-items: center;
  z-index: 100; }
  .login .button {
    font-family: "Orbitron", sans-serif;
    min-width: 75px;
    cursor: pointer;
    display: block;
    font-size: 0.7rem;
    line-height: 35px;
    border: 1px solid #7FF071;
    border-radius: 5px;
    color: #ede4d7;
    margin-left: 15px;
    padding: 0 10px;
    max-width: 100px;
    overflow: hidden;
    text-overflow: ellipsis; }
    .login .button:hover {
      color: #7FF071; }
  .login .signup {
    background: linear-gradient(#66b62d, #457c1e);
    min-width: 75px;
    font-family: "Orbitron", sans-serif;
    cursor: pointer;
    display: block;
    font-size: 0.7rem;
    line-height: 35px;
    border: 1px solid #7FF071;
    border-radius: 5px;
    color: #ede4d7;
    margin: 0 0px;
    padding: 0 10px; }
    .login .signup:hover {
      color: #7FF071; }
  .login .form {
    box-sizing: border-box;
    position: absolute;
    top: 40px;
    right: 131px;
    width: 300px;
    display: flex;
    flex-flow: column;
    justify-content: center;
    padding: 20px;
    border-radius: 5px;
    border: 1px solid #7FF071;
    background: #1C1818; }
    .login .form input {
      padding: 15px;
      margin: 5px;
      outline: none;
      appearance: none;
      border-radius: 5px;
      border: 1px solid #598053;
      background: #ede4d7; }
      .login .form input[type="submit"] {
