      border-radius: 5px;
      border: 1px solid #598053;
      background: #ede4d7; }
      .login .menu input[type="submit"] {
        background: linear-gradient(#66b62d, #457c1e);
        font-family: "Orbitron", sans-serif;
        color: #ede4d7;
        cursor: pointer;
        border: 1px solid #7FF071; }

