.gameBox {
  width: 300px;
  height: 400px;
  padding: 5% 0% 5% 0;
  margin: 0px 50px; }
  .gameBox .gameImage {
    border: 2px solid #598053;
    position: relative;
    width: auto;
    height: 400px;
    border-radius: 10px;
    background-size: cover;
    transition: .3s ease-in;
    background-position: center; }
    .gameBox .gameImage .frontImage {
      position: absolute;
      bottom: 0;
      width: auto;
      right: -25%;
      left: -25%;
      height: 110%;
      border: none;
      margin-left: auto;
      margin-right: auto; }
    .gameBox .gameImage:hover .textInImage {
      opacity: 1;
      top: 0;
      bottom: 0; }
    .gameBox .gameImage:hover .frontImage {
      filter: brightness(10%);
      transition: 0.2s ease-out; }
    .gameBox .gameImage .textInImage {
      display: flex;
      justify-content: center;
      flex-flow: column;
      background-color: rgba(0, 0, 0, 0.5);
      position: absolute;
      opacity: 0;
      top: 200px;
      bottom: 0px;
      left: 0;
      right: 0;
      border-radius: 10px;
      transition: .2s ease-in-out; }
      .gameBox .gameImage .textInImage h3 {
        padding: 5px;
        font-size: 1.3rem; }
      .gameBox .gameImage .textInImage h1 {
        font-size: 2rem; }
      .gameBox .gameImage .textInImage .gamemodes {
        position: absolute;
        padding: 0 10%;
        text-align: center;
        color: #ede4d7; }
      .gameBox .gameImage .textInImage hr {
        background-color: #598053;
        height: 4px;
        width: 80%;
        border: none;
        text-align: left; }
  .gameBox .label {
    margin: 5% 0;
    padding: 0 5%;
    width: 100%;
    text-align: left;
    border-left: 4px solid #598053; }

@media (max-width: 957px) {
  .gameBox {
    margin: 50px 0px; } }

@media (max-width: 540px) {
  .gameBox {
    width: 150px;
    height: 210px; }
    .gameBox .gameImage {
      height: 210px; }
      .gameBox .gameImage .frontImage {
        position: absolute;
        bottom: 0;
        width: auto;
        right: -25%;
        left: -25%;
        height: 110%;
        border: none;
        margin-left: auto;
        margin-right: auto; } }
