.concept {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column wrap;
  width: 100%;
  min-height: 100vh;
  box-sizing: border-box;
  padding: 135px 20px 20px 20px; }
  .concept .wrappers {
    display: flex;
    justify-content: center;
    flex-flow: column wrap;
    max-width: 900px;
    margin-bottom: 80px; }
    .concept .wrappers .container {
      display: grid;
      grid-template-rows: auto;
      padding-left: 0%;
      padding-right: 0%;
      justify-content: space-between; }
      .concept .wrappers .container .text {
        grid-area: text;
        text-align: left;
        display: flex;
        flex-flow: column;
        justify-content: flex-start; }
      .concept .wrappers .container .img {
        grid-area: img;
        display: flex;
        align-items: center; }
        .concept .wrappers .container .img img {
          height: 200px; }

.sideNav {
  position: fixed;
  left: 0;
  top: calc(50% - 125px);
  background-color: rgba(17, 17, 17, 0.4);
  height: 250px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  align-items: flex-start; }
  .sideNav .link {
    cursor: pointer;
    display: block;
    font-size: 14px;
    color: #ede4d7;
    padding: 0 20px;
    font-family: "Orbitron", sans-serif; }
    .sideNav .link:hover {
      color: #7FF071; }
  .sideNav .active {
    color: #7FF071; }

@media (max-width: 957px) {
  .concept {
    padding: 180px 20px 20px 20px; }
    .concept .wrappers .container {
      grid-template-areas: "img"  "text" !important;
      grid-template-columns: 100% !important; }
      .concept .wrappers .container .text {
        max-width: 450px; }
      .concept .wrappers .container .img {
        margin-bottom: 20px; }
        .concept .wrappers .container .img img {
          height: 100px; } }

@media only screen and (max-width: 1306px) {
  .sideNav {
    display: none; } }

@media only screen and (max-width: 856px) {
  .concept {
    text-align: center; }
    .concept .wrappers {
      justify-content: center; }
      .concept .wrappers .container .text {
        text-align: center; }
      .concept .wrappers .container .img {
        justify-content: center; } }

