.box {
  width: 180px; }
  .box .rbbIcon {
    width: 100%;
    padding-bottom: 20px; }
  .box .textUnder {
    width: 100%; }
    .box .textUnder .label {
      text-align: left;
      padding: 0 9%;
      border-left: 4px solid #598053; }
    .box .textUnder p {
      width: 100%;
      box-sizing: border-box;
      padding-left: calc(9% + 4px);
      text-align: left; }
