.results {
  text-align: left;
  color: #ede4d7;
  margin: 0 auto;
  width: 100%;
  max-width: 900px; }
  .results h1 {
    padding: 40px 20px 0 20px; }
  .results .result {
    padding: 20px;
    background: rgba(2, 2, 2, 0.2);
    border: 2px solid #598053;
    border-radius: 10px;
    margin: 10px 0; }
    .results .result span {
      min-width: 300px;
      cursor: pointer;
      display: grid;
      grid-template-columns: auto 50px;
      grid-template-rows: auto;
      grid-template-areas: "header button";
      align-items: center; }
      .results .result span img {
        grid-area: "button";
        height: 18px; }
      .results .result span h4 {
        grid-area: "header"; }
    .results .result p {
      margin: 10px 0; }
