.searchbar input, .searchbar button {
  appearance: none;
  outline: none;
  border: none;
  background: none; }

.searchbar {
  border: 2px solid #598053;
  border-radius: 10px;
  background-color: #ede4d7;
  color: #1C1818;
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: auto 50px;
  grid-template-rows: 50px;
  grid-template-areas: "input button"; }
  .searchbar input {
    grid-area: "input";
    padding: 0 20px;
    font-size: 0.8rem; }
    .searchbar input::placeholder {
      color: linear-gradient(#1C1818, #32322F);
      font-size: 0.8rem; }
  .searchbar button {
    grid-area: "button";
    display: flex;
    justify-content: center;
    align-items: center; }
    .searchbar button img {
      cursor: pointer;
      height: 18px; }
