.BigButton {
  display: block;
  border-radius: 10px;
  border: none;
  width: calc((100% - 60px) / 3);
  padding: 15px 0px;
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center; }
  .BigButton img {
    position: relative;
    width: 20%;
    align-content: center; }
  .BigButton h3 {
    font-size: 12px; }
  .BigButton .line {
    position: relative;
    display: block;
    margin: 10px 0;
    width: 40%;
    height: 2px;
    opacity: 0.7; }
  .BigButton:hover .line {
    transition: .5s;
    transform: scale(0.3, 1); }

@media (max-width: 482px) {
  .BigButton {
    width: 100%;
    margin-bottom: 15px; }
    .BigButton img {
      width: 10%; }
    .BigButton h3 {
      font-size: 12px; }
    .BigButton .line {
      width: 40%; } }
